import React from "react";
import classes from './Post.module.css';

let {item} = classes;

const Post = (props) => {
    return (
        <div className={item}>
            <img
                src='data:image/jpeg;base64,'/>
            {props.message}
            <div>
                <span>like {props.likesCount}</span>
            </div>
        </div>
    )
};

export default Post;